// =============================================
// VARIABLES MENU
//
// 1. BREAKPOINTS
// 2. BASIC VARIABLES
// 3. DESIGN: DEFAULT
// 4. DESIGN: FLAT
// 5. DESIGN: BOLD
// 6. DESIGN: MATERIAL
// =============================================

// =============================================
// 1. BREAKPOINTS

$fui-breakpoints: (
	sm: 783px,
	md: 961px,
	lg: 1201px
) !default;

// =============================================
// 2. BASIC VARIABLES

$gutter:                30px        !default;
$gutter-sm:             20px        !default;
$gutter-enclosed:       14px        !default;
$gutter-enclosed-sm:    8px         !default;

$transition--ease:      0.2s ease   !default;
$transition--linear:    0.2s linear !default;

$input--height:         40px        !default;
$input--padding:        10px        !default;
$input--line-height:    20px        !default;

$textarea--height:      200px       !default;
$textarea--padding:     10px        !default;
$textarea--line-height: 20px        !default;

// =============================================
// 3. DESIGN: DEFAULT

$default--border-width:  1px   !default;
$default--border-style:  solid !default;
$default--border-radius: 2px   !default;

// =============================================
// 4. DESIGN: FLAT

// =============================================
// 5. DESIGN: BOLD
$bold--border-width: 3px   !default;
$bold--border-style: solid !default;

// =============================================
// 6. DESIGN: MATERIAL
$material--border-width:           1px   !default;
$material--border-style:           solid !default;

$material--textarea-border-radius: 2px   !default;